.faq {
  padding: 50px 0;
  background-color: #f1f4f5;
  .title {
    text-align: center;
    h3 {
      font-size: 33px;
      font-family: 'Montserrat', sans-serif;
      color: rgb(47, 56, 61);
      font-weight: bold;
      margin-bottom: 35px;
    }
  }
  form {
    .input-group-prepend {
      position: relative;
      .sm-block({
        display: block;
        text-align: center;
     
      });
      i {
        position: absolute;
        top: 15px;
        left: 10px;
      }
      input {
        padding: 13px 0;
        width: 100%;
        padding-left: 6%;
        .sm(padding-left, 10%);
        border-radius: 5px;
        background-color: rgb(255, 255, 255);
        border: none;
        &::placeholder {
          font-size: 16px;
          font-family: 'Montserrat', sans-serif;
          color: rgb(117, 139, 152);
        }
      }
      button {
        .sm-block({
       position: relative;
       border-radius: 5px;
       margin: 10px;
        });
        position: absolute;
        right: 0;
        top: 0;
        z-index: 100;
        background-color: rgb(143, 158, 167);
        border: none;
        font-size: 16px;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        padding: 13px 30px;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
      }
    }
  }
  .nav {
    padding: 30px 0;
    border: none;
    justify-content: center;
    .nav-item {
      width: 130px;
      margin: 15px;
      .sm(margin,10px 0);
      a {
        text-align: center;
        padding: 0;
        border: none;
        &.active {
          background: transparent;
          filter: grayscale(1);
        }
        img {
          margin-bottom: 20px;
        }
        h4 {
          font-size: 14px;
          font-family: 'Montserrat', sans-serif;
          color: rgb(143, 158, 167);
          margin: 0;
        }
      }
    }
  }
  .tab-content {
    .tab-pane {
      .content {
        .query {
          width: 100%;
          text-align: center;
          position: absolute;
          bottom: 2%;
          font-size: 14px;
          font-family: 'Montserrat', sans-serif;
          color: rgb(217, 180, 101);
          text-decoration: underline;
          i {
            margin-left: 5px;
          }
        }
        position: relative;
        justify-content: space-between;
        display: flex;
        flex-wrap: wrap;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
        padding: 40px 15px;
        .close {
          position: absolute;
          right: 2%;
          top: 2%;
          .md(top, 1%);
          .md(right, 4% );
        }
        .lineyka {
          background-image: -moz-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
          background-image: -webkit-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
          background-image: -ms-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
          width: 1px;
          height: 593px;
          .md-block({
            width: 100%;
            height: 1px;
          })
        }
        ul {
          width: 45%;
          .md(width, 100%);
          padding: 0;
          li {
            list-style: none;
            margin: 10px 0;
        
            a {
              text-decoration: none;
              font-size: 14px;
              font-family: 'Montserrat', sans-serif;
              color: rgb(47, 56, 61);
              .sm(font-size, 12px);
              i {
                margin-right: 5px;
              }
              &:hover {
                color: #d9b465;
              }
            }
          }
        }
      }
    }
  }
  .question {
    border-radius: 5px;
    background-color: rgb(247, 206, 120);
    box-shadow: inset 0px -3px 0px 0px rgba(197, 157, 84, 0.004);
    font-size: 16px;
    color: rgb(47, 56, 61);
    text-transform: uppercase;
    width: 234px;
    height: 50px;
    display: block;
    margin: auto;
    margin-top: 30px;
  }
}