.mobile {
  background: url('../img/dark.png') center;
  background-size: cover;
 padding-top: 50px;
  .mobile-title {
    .lg-block({
      text-align: center;
    });
    h3 {
      font-size: 33px;
      font-family: 'Montserrat', sans-serif;
      color: rgb(247, 206, 120);
      font-weight: bold;
    }
    p {
      font-size: 16px;
      font-family: 'Montserrat', sans-serif;
      color: rgb(255, 255, 255);
      margin-bottom: 20px;
    }
    .help {
      display: flex;
      flex-wrap: wrap;
      .lg-block({
     justify-content: center;
      });
      .item {
        width: 150px;
     margin-bottom: 15px;
     margin-top: 15px;
        img {
          
          display: block;
          margin: auto;margin-bottom: 20px;
        }
        h5 {
          font-size: 14px;
          font-family: 'Montserrat', sans-serif;
          color: rgb(255, 255, 255);
          text-align: left;
        }
      }
    }
    .register {
      &:hover {
        background-color: rgb(197, 157, 84);
      }
      border-radius: 5px;
      background-color: rgb(247,
      206,
      120);
      box-shadow: inset 0px -3px 0px 0px rgba(197,
      157,
      84,
      0.004);
      width: 215px;
      height: 50px;
      .lg-block({
            display: block;
      margin: auto;
      });
    
    }
  }
  .apps{
      align-self: flex-end;
      text-align: center;
      position: relative;
      .lg-block({
      padding-top: 50px;
         });
     /*  .hand{
        position: absolute;
      bottom: -70%;
      right: 15%;
      } */
      .appstore{
        position: absolute;
        bottom: 5%;
        width: 100%;
        text-align: center;
        left: 0;
           a{
      
      
          img{
               margin: 10px;
            .media-block({
              width: 40%;
            },500px);
          }
         
      }
      }
   
  }
}