header {
  .header-top {
    background-color: rgb(117, 139, 152);
    ul {
      padding: 0;
      display: flex;
      flex-wrap: wrap;
      margin: 0;
      justify-content: space-between;
      .xl(justify-content, center);
      li {
        .sm-block( {
          width: 100%;
          justify-content: center;
        }
        );
        display: flex;
        list-style: none;
        align-self: center;
        padding: 5px 0;
        &:last-child {
          &::after {
            display: none;
            margin-left: 20px;
          }
        }
        p {
          font-size: 12px;
          margin: 0;
          color: #fff;
          align-self: center;
        }
        &::after {
          content: "";
          background-image: -moz-linear-gradient( 90deg, rgba(197, 157, 84, 0) 0%, rgb(197, 157, 84) 50%, rgba(197, 157, 84, 0) 100%);
          background-image: -webkit-linear-gradient( 90deg, rgba(197, 157, 84, 0) 0%, rgb(197, 157, 84) 50%, rgba(197, 157, 84, 0) 100%);
          background-image: -ms-linear-gradient( 90deg, rgba(197, 157, 84, 0) 0%, rgb(197, 157, 84) 50%, rgba(197, 157, 84, 0) 100%);
          width: 1px;
          height: inherit;
          margin: 0 10px;
        }
        .kurs {
          .sm-block( {
            flex-direction: column;
          }
          );
          li {
            text-transform: uppercase;
            font-weight: 500;
            &::after {
              display: none;
            }
            margin:0 6px;
            i {
              display: flex;
              align-self: center;
              color: #8f9ea7;
              &::before {
                padding-right: 5px;
              }
            }
            font-size: 12px;
            .usd {
              color: #fff;
            }
            .number {
              color: rgb(239, 202, 123);
              margin: 0 5px;
            }
          }
        }
        .lang {
          &.active {
            color: rgb(239, 202, 123);
          }
          font-size: 12px;
          text-decoration: underline;
          color: #fff;
          margin: 0 5px;
        }
        img {
          margin-right: 8px;
          width: 15px;
          &.phone {
            margin-right: 0;
            margin-left: 20px;
          }
        }
        button {
          background: transparent;
          border: none;
        }
      }
    }
  }
  .header-body {
    background-color: rgb(117, 139, 152);
    padding: 25px 0;
    border-bottom: 1px solid #46535b;
    .body-center{
      align-self: center;
    }
    .name {
      .postda {
        display: flex;
        .lg-block( {
          justify-content: center;
        }
        );
        &::after {
          content: "UZ";
          justify-content: center;
          background-color: rgb(47, 56, 61);
          width: 38px;
          height: 21px;
          display: flex;
          -moz-transform: rotate(-5deg) skewX(-5deg) skewY(0deg);
          -webkit-transform: rotate(-5deg) skewX(-5deg) skewY(0deg);
          -o-transform: rotate(-5deg) skewX(-5deg) skewY(0deg);
          -ms-transform: rotate(-5deg) skewX(-5deg) skewY(0deg);
          transform: rotate(-5deg) skewX(-5deg) skewY(0deg);
          color: rgb(247, 206, 120);
        }
      }
      .post-title {
        h5 {
          font-size: 13px;
          color: rgb(255, 255, 255);
          text-transform: uppercase;
          line-height: 1.2;
          max-width: 205px;
          .lg-block( {
            max-width: 100%;
          }
          );
        }
      }
    }
    .logo {
      text-align: center;
      img {
        .lg-block( {
          margin: 30px auto;
        }
        );
      }
    }
    .murojat {
      display: flex;
      align-self: center;
      padding: 0;
      justify-content: center;
      .main {
        display: flex;
        .lg-block({
          flex-direction: column;
        });
        .murojat-img {
          margin: 5px;
          align-self: center;
        }
        .murojat-title {
          font-size: 14px;
          color: rgb(255, 255, 255);
          line-height: 1.2;
          margin: 5px;
          align-self: center;
          p {
            margin: 0;
          }
        }
        &::after {
          content: "";
          height: 100px;
          width: 1px;
          background: #46535b;
          margin: 0 20px;
          align-self: center;
          .lg(display, none);
        }
      }
      ul {
        padding: 0;
        align-self: center;
        li {
          list-style: none;
          display: flex;
          img {
            margin: 10px 5px;
          }
          a {
            font-size: 14px;
            color: rgb(255, 255, 255);
            line-height: 1.429;
            margin: 5px;
          }
        }
      }
    }
  }
  .sina-nav {
    background: #647681 !important;
    .social-on{
   .md-block({
  height: 66.5px !important;});
  .sina-brand{
    .md-block({
        margin-top: 0 !important;
    }) ;
  }
    }
    .sina-brand{
      display: flex;
    
    img{
      width: 100% ;
      height: auto;
      align-self: center;
    }  
    }
    &.navbar-freez {
      .navbar-collapse {
        .sina-menu {
          li {
            a {
              font-size: 14px;
              .xl-block( {
                font-size: 12px;
              }
              );
            }
            .dropdown-menu {
              li {
                a {
                  font-size: 14px;
                  .xl-block( {
                    font-size: 12px;
                  }
                  );
                }
              }
            }
          }
        }
      }
    }
    .navbar-collapse {
      .sina-menu {
        float: none;
        display: flex;
        justify-content: center;
        .media-block({
          flex-direction: column;
          background: #46535b;
          li{
            &::after{
              display: none;
            }
            a{
              margin: auto;
            }
          }
        },1024px);
        li {
          display: flex;
          &:hover {
            background-color: rgb(35, 42, 46);
            a {
              color: rgb(247, 206, 120);
              background: transparent;
            }
          }
          &:last-child{
            &::after{
              display: none;
            }
          }
          &::after {
            content: "";
            background-image: -moz-linear-gradient( 90deg, rgba(70, 83, 91, 0) 0%, rgb(70, 83, 91) 50%, rgba(70, 83, 91, 0) 100%);
            background-image: -webkit-linear-gradient( 90deg, rgba(70, 83, 91, 0) 0%, rgb(70, 83, 91) 50%, rgba(70, 83, 91, 0) 100%);
            background-image: -ms-linear-gradient( 90deg, rgba(70, 83, 91, 0) 0%, rgb(70, 83, 91) 50%, rgba(70, 83, 91, 0) 100%);
            width: 1px;
            height: 40px;
            align-self: center;
          }
          a {
            font-weight: 500;
            font-size: 16px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(247, 206, 120);
            background: transparent;
            border: none;
            &::after{
              .media-block({
                display: none !important;
              },1024px);
            }
            .xl-block( {
              font-size: 14px;
              padding: 20px 10px;
            }
            );
          }
          .dropdown-menu {
            .media-block({
              display: none !important;
            },1024px);
            li {
           border-bottom: 1px solid #232a2e;
                background-color: rgb(47, 56, 61);
                &:hover {
                  background-color: rgb(255, 211, 119);
                
                  color: rgb(47, 56, 61);
                  
                }
                a{
                  background-color: transparent;
                  border: none;
                &:hover {
                  background-color: transparent;
               
                  color: rgb(47, 56, 61);
                  
                }
                  
                }
             &::after{
               display: none;
             }
            }
          }
        }
      }
    }
  }
}