.main {
  padding: 50px 0;
  .intro {
    .slider {
      .slider-item {
        position: relative;
        .slider-img {
          position: relative;
          img {
            width: 100%;
         
          }
          .info {
            .md-block( {
              display: none;
            }
            );
            position: absolute;
            padding: 0 3%;
            bottom: 0%;
            width: 100%;
            display: flex;
            justify-content: space-between;
            background-image: -moz-linear-gradient( 90deg, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
            background-image: -webkit-linear-gradient( 90deg, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
            background-image: -ms-linear-gradient( 90deg, rgb(0, 0, 0) 0%, rgba(0, 0, 0, 0) 100%);
            opacity: 0.502;
            flex-wrap: wrap;
            .data {
              display: flex;
              align-self: center;
              margin: 5px;
              p {
                font-size: 12px;
                color: rgb(242, 242, 242);
                margin: 0 10px;
                i {
                  margin-right: 5px;
                }
              }
            }
            .social {
              display: flex;
              margin: 5px;
              p {
                margin: 0;
                font-size: 12px;
                color: rgb(242, 242, 242);
              }
              a {
                margin: 0 5px;
                background: #fff;
                padding: 2px;
                width: 25px;
                height: 25px;
                text-align: center;
                transform: translateY(-5px);
                i {
                  color: #7f7e82;
                }
              }
            }
          }
        }
        .slider-text {
          display: flex;
          /*   transform: translateY(-55px); */
          border-radius: 5px;
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
          height: 80px;
          background-color: rgb(117, 139, 152);
          .md-block( {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: rgb(100, 118, 129);
            opacity: 0.851;
            border-radius: 0;
            height: auto;
            overflow: hidden;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
          }
          );
          .xl-block( {
            /*    transform: translateY(-44px); */
            padding: 0 1%;
          }
          );
          .md-block( {
            /*    transform: translateY(-32px);
         */
          }
          );
          padding: 0 5%;
          background: #fff;
          justify-content: space-between;
          p {
            font-size: 22px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(100, 118, 129);
            font-weight: bold;
            width: 86%;
            display: flex;
            margin: 0;
            align-self: center;
            .md-block( {
           font-size: medium;
              color: rgb(255, 255, 255);
              width: 100%;
            }
            );
            .sm-block({
              font-size: small;
            });
            &::after {
              content: "";
              .md-block( {
       display: none;
                 }
                 );
              background-image: -moz-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
              background-image: -webkit-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
              background-image: -ms-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
              width: 1px;
              height: 60px;
              align-self: center;
            }
          }
        }
      }
      button {
        z-index: 60;
        top: 92%;
        .md-block( {
          display: none;
        }
        );
        &.slick-prev {
          right: 8%;
          left: auto;
          &::before {
            content: url('../img/left.png');
          }
        }
        &.slick-next {
          right: 2%;
          &::before {
            content: url('../img/right.png');
          }
        }
      }
      .slick-dots {
        bottom: 25%;
        .xl(bottom,
        30%);
        li {
          &.slick-active {
            button {
              &::before {
                content: "";
                background-color: rgb(247, 206, 120);
                border-color: rgb(247, 206, 120);
              }
            }
          }
          button {
            &::before {
              content: "";
              border-radius: 50%;
              border: #fff 2px;
              width: 10px;
              height: 10px;
              background: #fff;
            }
          }
        }
      }
    }
    .news-md {
      .card {
        margin: 15px;
        border-radius: 5px;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
        &:hover {
          transition: all 1500ms;
          background-color: rgb(247, 206, 120);
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
        }
        .card-top {
          position: relative;
          img {
            width: 100%;
          }
          span {
            position: absolute;
            border-radius: 3px;
            background-color: rgb(0, 0, 0);
            opacity: 0.502;
            left: 10px;
            bottom: 10px;
            font-size: 12px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(255, 255, 255);
            line-height: 1.2;
          }
        }
        .card-body {
          .newsp {
            font-size: 14px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(47, 56, 61);
            line-height: 1.2;
            margin-bottom: 20px;
            height: 70px;
            overflow: hidden;
          }
          padding: 5%;
          .card-data {
            display: flex;
            p {
              i {
                margin-right: 5px;
              }
              font-size: 12px;
              color: rgb(143,
              158,
              167);
              line-height: 1.2;
              margin-right: 20px;
              margin-bottom: 0;
            }
          }
        }
      }
    }
    .news-lg {
      .lg-item {
        display: flex;
        margin: 15px;
        border-radius: 5px;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
        .md-block( {
          flex-direction: column;
          text-align: center;
        }
        );
        &:hover {
          transition: all 1500ms;
          background-color: rgb(247, 206, 120);
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
        }
        .lg-img {
          position: relative;
          width: 30%;
          .md-block( {
            width: 100%;
          }
          );
          img {
            width: 100%;
            height: 100%;
          }
          a {
            position: absolute;
            left: 10px;
            bottom: 10px;
            padding: 5px;
            border-radius: 3px;
            background-color: rgb(0, 0, 0);
            opacity: 0.502;
            font-size: 12px;
            font-family: ' Montserrat', sans-serif;
            color: rgb(255, 255, 255);
            line-height: 1.2;
          }
        }
        .lg-title {
          .md-block( {
            width: 100%;
          }
          );
          align-self: center;
          width: 70%;
          padding: 15px 20px;
          .lg-text {
            font-size: 14px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(47, 56, 61);
            font-weight: 500;
            line-height: 1.2;
            margin-bottom: 10px;
            height: 85px;
            overflow: hidden;
          }
          h3 {
            font-size: 16px;
            font-family: 'Montserrat', sans-serif;
            color: rgb(47, 56, 61);
            font-weight: bold;
            line-height: 1.2;
            margin-bottom: 20px;
          }
          .lg-date {
            display: flex;
            p {
              i {
                margin-right: 5px;
              }
              font-size: 12px;
              color: rgb(143,
              158,
              167);
              line-height: 1.2;
              margin-right: 20px;
              margin-bottom: 0;
            }
          }
        }
      }
    }
    .allnews {
      border-radius: 5px;
      background-color: rgb(247, 206, 120);
      box-shadow: inset 0px -3px 0px 0px rgba(197, 157, 84, 0.004);
      width: 209px;
      height: 50px;
      font-size: 16px;
      color: rgb(47, 56, 61);
      text-transform: uppercase;
      line-height: 1.2;
      display: block;
      margin: 15px auto;
      &:hover {
        background-color: rgb(197, 157, 84);
      }
    }
  }
}