footer {
  padding: 50px 0;
        background-color: rgb(35, 42, 46);
      img{
          margin-bottom: 30px;
      }
  ul {
    padding: 0;
    li {
      display: flex;
      list-style: none;  
      justify-content: center;
      &:hover{
          a{
              color: #f7ce78;
          }
      }
      span {
        display: flex;
        flex-wrap: wrap;
        a{
            line-height: 0;
        }
      }
      a {
          text-decoration: none !important;
        padding: 7px;
     font-weight: 500;
        font-size: 12px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(153, 153, 153);
      }
      p{
        font-size: 12px;
        font-family: 'Montserrat', sans-serif;  
        color: #f7ce78;
        font-weight: 500;
        margin: 0;
        padding: 7px 0;
      }
    }
  }
}