.main {
  .sidebar {
    .newspaper {
      position: relative;
      .gazeta {
        width: 100%;
      }
      span {
        position: absolute;
        text-align: center;
        left: -5px;
        bottom: 25px;
        background-image: -moz-linear-gradient( 30deg, rgb(231, 184, 84) 0%, rgb(255, 211, 119) 20%, rgb(255, 223, 155) 49%, rgb(255, 211, 118) 74%, rgb(231, 184, 84) 100%);
        background-image: -webkit-linear-gradient( 30deg, rgb(231, 184, 84) 0%, rgb(255, 211, 119) 20%, rgb(255, 223, 155) 49%, rgb(255, 211, 118) 74%, rgb(231, 184, 84) 100%);
        background-image: -ms-linear-gradient( 30deg, rgb(231, 184, 84) 0%, rgb(255, 211, 119) 20%, rgb(255, 223, 155) 49%, rgb(255, 211, 118) 74%, rgb(231, 184, 84) 100%);
        width: 85px;
        height: 23px;
        font-size: 16px;
        color: rgb(47, 56, 61);
      }
    }
    button {
      width: 100%;
      height: 50px;
      font-size: 16px;
      border-radius: 5px;
      margin:20px auto;
      display: block;
     
      &.dowload {
        background-color: rgb(255, 211, 119);
        box-shadow: inset 0px -3px 0px 0px rgba(197, 157, 84, 0.004);
        color: rgb(47, 56, 61);
        &:hover {
          background-color: rgb(197, 157, 84);
        }
      }
      &.choise {
        background-color: rgb(143, 158, 167);
        box-shadow: inset 0px -3px 0px 0px rgba(100, 118, 129, 0.004);
        color: rgb(255, 255, 255);
        &:hover {
          background-color: rgb(100, 118, 129);
        }
      }
    }
  }
  .tg-bot {
    margin: 20px auto;
    position: relative;
    border-radius: 5px;
    background-color: rgb(117, 139, 152);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
    img {
      object-fit: cover;
      width: 100%;
    }
    .bot-title {
      background-color: rgb(255, 211, 119);
      height: 50px;
      display: flex;
      margin-bottom: 20px;
      h3 {
        display: block;
        margin: auto;
        font-size: 16px;
        font-family: 'Montserrat', sans-serif;
        color: rgb(47, 56, 61);
        font-weight: bold;
        text-transform: uppercase;
      }
    }
    .tg-button{
      position: absolute;
    width: 100%;
      bottom: 20px;
      button {
        text-decoration: none;
        border-radius: 5px;
        background-color: rgb(255, 211, 119);
        box-shadow: inset 0px -3px 0px 0px rgba(197, 157, 84, 0.004);
        width: 80%;
        height: 50px;
      
        &:hover {
          background-color: rgb(197, 157, 84);
        }
      }
    }
  
  }
  .special-news {
    font-family: 'Montserrat', sans-serif;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
    margin: 15px 0;
    .special-title {
      background-color: rgb(255, 211, 119);
      height: 50px;
      display: flex;
      margin-bottom: 10px;
      background-image: -moz-linear-gradient( -90deg, rgba(0, 0, 0, 0.10196) 0%, rgba(0, 0, 0, 0) 100%);
      background-image: -webkit-linear-gradient( -90deg, rgba(0, 0, 0, 0.10196) 0%, rgba(0, 0, 0, 0) 100%);
      background-image: -ms-linear-gradient( -90deg, rgba(0, 0, 0, 0.10196) 0%, rgba(0, 0, 0, 0) 100%);
      h3 {
        display: block;
        margin: auto;
        font-size: 16px;
        color: rgb(47, 56, 61);
        font-weight: bold;
        text-transform: uppercase;
      }
    }
    .special-content {
      padding-left: 15px;
      .special-item {
        .lg-block( {
          text-align: center;
        }
        );
        &:last-child {
          &::after {
            display: none;
          }
        }
        &::after {
          content: "";
          background-image: -moz-linear-gradient( 0deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
          background-image: -webkit-linear-gradient( 0deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
          background-image: -ms-linear-gradient( 0deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
          width: 255px;
          height: 1px;
          display: block;
          margin-top: 10px;
          .lg-block( {
            margin: auto;
          }
          );
        }
        padding: 5px 0;
        .news {
          font-size: 14px;
          color: rgb(47, 56, 61);
          margin-bottom: 12px;
        }
        .item-date {
          display: flex;
          p {
            font-size: 12px;
            color: rgb(143, 158, 167);
            margin-right: 15px;
            margin-bottom: 0;
            i {
              margin-right: 5px;
            }
          }
        }
      }
    }
    .special-all {
      background-color: rgb(143, 158, 167);
      height: 51px;
      display: flex;
      a {
        display: flex;
        margin: auto;
        font-size: 14px;
        color: rgb(255, 255, 255);
        text-decoration: underline;
        i {
          margin-left: 10px;
          align-self: center;
        }
      }
    }
  }
  .rubrics {
    margin: 15px 0;
    .rubric-title {
      background-color: rgb(247, 206, 120);
      display: flex;
      height: 50px;
      h3 {
        display: block;
        margin: auto;
        font-size: 16px;
        color: rgb(47, 56, 61);
        font-weight: bold;
        text-transform: uppercase;
      }
    }
    ul {
      padding-left: 15px;
      padding-bottom: 10px;
      border-radius: 5px;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
      .lg-block({
         display: flex;
         flex-wrap: wrap; 
         justify-content: space-between;
        
      }); .sm(justify-content, center);
      li {
        list-style: none;
        padding: 5px 0;
     flex-basis: 240px;
     
        a {
          font-size: 14px;
          color: rgb(47, 56, 61);
          &:hover {
            color: #d9b465;
          }
        }
      }
    }
  }
  .weather {
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
    padding: 10px;
    .dropdown {
      .dropdown-toggle {
        width: auto;
        font-size: 16px;
        color: rgb(247, 206, 120);
        text-transform: capitalize;
      }
      .dropdown-menu {
        .dropdown-item {
          font-size: 16px;
          color: rgb(247, 206, 120);
          text-transform: capitalize;
        }
      }
    }
    .rain {
      text-align: center;
      .rain-img {
        margin-right: 15px;
      }
      .after {
        font-size: 30px;
        color: rgb(100, 118, 129);
      }
      .evening {
        font-size: 20px;
        color: rgb(100, 118, 129);
      }
    }
  }
}