.video-news {
  padding: 50px 0;
  background: url('../img/videobg.png') center;
  background-size: cover;
  .title {
    text-align: center;

    h3 {
      font-size: 33px;
      font-family: 'Montserrat', sans-serif;
      color: rgb(247, 206, 120);
      font-weight: bold;
      margin-bottom: 15px;
      .sm(font-size,18px);
    }
  }
  .video-slider {
    .video-item {
        border-radius: 5px;
      margin: 15px;
  
      .item-top {
        position: relative;
      .play{
        position: absolute;
        top: 50%;
  width: 100%;

            a {
            &:hover{
          
                background: #d5b66a;
          i{
              color: #fff;
          }
      
        
      }
          text-decoration: none;
       margin: auto;
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background: #000;
          opacity: .8;
          text-align: center;
          display: flex;
          i {
            display: block;
            margin: auto;
            color: #d5b66a;
          }
        }
      }
        
      
        img{
            width: 100%;
        }
      }
      .item-body {
        padding: 15px;
        background-color: rgb(100, 118, 129);
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
        p{
               font-size: 14px;
        color: rgb(255, 255, 255);
        font-weight: bold;
        margin-bottom: 10px;
        height: 60px;
        overflow: hidden;
        }
        .time{
            display: flex;
            span{
           &:nth-child(1){
               margin-right: 15px;
           }
                    font-size: 12px;
                    color: rgb(204, 204, 204);
                  i{
                      margin-right: 5px;
                  }
            }
        }
     
      }
    }
  }
}
