.social2 {
  font-family: ' Montserrat', sans-serif;
  padding: 50px 0;
  .title {
    text-align: center;
    h3 {
      font-size: 30px;
      color: rgb(100, 118, 129);
      font-weight: bold;
      margin-bottom: 50px;
    }
  }
  .social-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
    .social-item {
      padding: 0 20px;
      display: flex;
      width: 24%;
      .lg-block({
         width: 48%; 
         margin: 15px 0;
         justify-content: center;
         &:nth-child(2){
          &::after{
              display: none;
         }
         };
      });
      .sm-block({
         flex-direction: column; 
      });
      position: relative;
      &:last-child{
          &::after{
              display: none;
          }
        };
      &::after {
        content: "";
     display: block;
        background-image: -moz-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
        background-image: -webkit-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
        background-image: -ms-linear-gradient( 90deg, rgba(204, 204, 204, 0) 0%, rgb(204, 204, 204) 50%, rgba(204, 204, 204, 0) 100%);
        width: 1px;
        height: 80px;
        position: absolute;
        right: 0;
        top: -24px;
        .sm(top, 0);
      }
      &:hover {
        i {
          color: #ff9800;
        }
        p {
          color: #232a2e;
        }
      }
      i {
        color: #95a3ac;
        font-size: 36px;
       padding-right: 20px;
       align-self: center;
       .sm(padding,10px 0);
      }
      p {
        font-size: 16px;
        color: rgb(143, 158, 167);
       align-self: center;
       margin: 0;
      }
    }
  }
}